<template>
  <div>
    <div class="col-xs-12 col-sm-12 col-md-12 toTop">
      <el-form :model="ruleForm" status-icon label-width="100px">
        <el-form-item label="姓名：" required>
          <el-input size="small" v-model="ruleForm.name" placeholder="请输入姓名" />
        </el-form-item>
        <el-form-item label="电话：" required>
          <el-input size="small" v-model="ruleForm.phone" placeholder="请输入电话" />
        </el-form-item>
        <el-form-item label="公司座机：" required>
          <el-input size="small" v-model="ruleForm.companyPhone" placeholder="请输入公司座机" />
        </el-form-item>
        <el-form-item label="公司名称：" required>
          <el-input size="small" v-model="ruleForm.companyName" placeholder="请输入公司名称" />
        </el-form-item>
        <el-form-item label="公司地址：" required>
          <el-input size="small" v-model="ruleForm.companyAddress" placeholder="请输入公司地址" />
        </el-form-item>
        <el-form-item label="住宅地址：" required>
          <el-input size="small" v-model="ruleForm.houseAddress" placeholder="请输入住宅地址" />
        </el-form-item>
        <el-form-item label="预约时间：" required>
          <el-date-picker
            size="small"
            v-model="ruleForm.time"
            type="datetime"
            value-format="yyyy-MM-dd-HH-mm-ss"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" @click="submitForm">提交</el-button>
          <el-button size="small" @click="goBackDetails">返回主页</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="bottomContent" style="margin-top: 45px">
      <span>唐经理：18617094201</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      ruleForm: {
        name: '', phone: '', companyPhone: '', companyName: '', companyAddress: '', houseAddress: '', time: ''
      }
    }
  },
  methods: {
    submitForm() {
      for(const v in this.ruleForm) {
        if (this.ruleForm[v] === ''){ this.$message.warning(`${v}选项不能为空！`); return }
      }
      $.ajax({
        data: this.ruleForm,
        type: 'get',
        url: 'http://localhost:9999/subForm'
      }).done((res)=>{
        const data = JSON.parse(res);
        if (data.ok) { this.$message.success('提交成功！') }
      }).fail((res)=>{ this.$message.error('服务器报错！') });
    },
    goBackDetails(){
      this.$router.push({ path: '/creditCarDetail' });
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.toTop{
  margin-top: 10px;
}
.bottomContent{
  width: 100%;
  height: 45px;
  background: red;
  text-align: center;
  line-height: 45px;
  color: #FFF;
  position: fixed;
  bottom: 0;
}
</style>



